import React from 'react';
import { createRoot } from 'react-dom/client';
import App from './App';
import './index.css';

const el = document.getElementById('root');
if (!el) {
  throw new Error('Could not find root element');
}

const root = createRoot(el);
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);